:root {
    --wp--color--normal-text: #141313;
    --wp--color--grey-text: #666;
    --wp--color--grey-v2-text: #42446E;
    --wp--color--black: #000;
    --wp--color--white: #FFF;
    --wp--gradient--orange-to-lilac: linear-gradient(273deg, #FFB147 0%, #FF6C63 52.03%, #B86ADF 100%);
    --wp--gradient--experience-bg: linear-gradient(270deg, #FFB147 5.74%, #FF6C63 50.64%, #B86ADF 92.05%);
    --wp--color--sidebar-bg: #141313;
    --wp--color--sidebar-menu-item: #FFF;
    --wp--color--sidebar-social-bg: #FFF;
    --wp--color--sidebar-social-border: rgba(20, 19, 19, .1);
    --wp--color--contact-border-bottom: rgba(20, 19, 19, .2);
    --wp--color--portfolio-owner-name: #B86ADF;
    --wp--color--quote-bg: #141313;
    --wp--color--quote-text: #FFF;
    --wp--transition: .4s ease-out;
}

#main-wrapper {
    width: 100vw;
    min-width: 100%;
    min-height: 100vh;
}

#wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

body {
    overflow-x: hidden;
    max-width: 100vw;
    width: 100vw;
    position: relative;
    min-height: 100vh;

    &.error404 {
        overflow: hidden;

        .content-wrapper {
            zoom: 1;
        }
    }

    &.popup-opened,
    &.burger-active {
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 100vh;
        }
    }

    &.burger-active {


        #main-wrapper {
            position: relative;
            z-index: 1;

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
                background-color: rgba(0, 0, 0, .6);
            }
        }
    }

    * {
        font-family: var(--wp--font-family--title);
        box-sizing: border-box;
        color: var(--wp--color--normal-text);
    }
}

%single-content-wrapper {
    width: calc(100% - 285px);
    overflow: hidden;

    @include max-break(1750px) {
        width: calc(100% - 265px);
    }

    @include max-break(1600px) {
        width: calc(100% - 250px);
    }

    @include max-break(1280px) {
        width: calc(100% - 210px);
    }

    @include max-break(1030px) {
        width: calc(100% - 170px);
    }

    @include max-break(768px) {
        width: 100vw;
        zoom: 1;
    }
}

%lang-select {
    .jq-selectbox {
        position: relative;
        z-index: 1;
        border: 1px solid transparent;

        &.opened {
            border: 1px solid #DEDDDD;
            border-top-left-radius: 12px;
            border-top-right-radius: 12px;
            border-bottom: unset;
            background-color: #101010;

            .jq-selectbox__dropdown {
                border-radius: 0 0 12px 12px;
                border: 1px solid #DEDDDD;
                border-top: unset;
                margin: 0;
                left: -1px;
                width: calc(100% + 2px);
                background-color: #101010;
            }

            .jq-selectbox__trigger-arrow::after {
                transform: translate(-50%, -50%) scaleY(-1);
            }
        }

        #lang_choice_1 {
            background-color: transparent;
            outline: none;
            border: unset;
            border: 1px solid transparent;
            width: 100%;
        }

        &__trigger,
        &__select {
            border: unset;
        }

        &__select {
            display: flex;
            align-items: center;
            color: unset;
            font: unset;
            height: unset;
            padding: 8px 28px 8px 8px;
            text-shadow: unset;
            position: relative;
            z-index: 1;
        }

        &__trigger {
            height: 24px;
            width: 24px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        &__trigger-arrow {
            border-left: unset;
            border-right: unset;
            border-top: unset;
            position: absolute;
            z-index: 1;
            height: 24px;
            width: 24px;
            top: 50%;
            left: 0;
            transform: translateY(-50%);

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 12px;
                height: 6px;
                z-index: 1;
                background: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTQiIGhlaWdodD0iOCIgdmlld0JveD0iMCAwIDE0IDgiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTEgMUw3IDdMMTMgMSIgc3Ryb2tlPSJ3aGl0ZSIgc3Ryb2tlLWxpbmVjYXA9InJvdW5kIiBzdHJva2UtbGluZWpvaW49InJvdW5kIi8+PC9zdmc+) center center no-repeat;
                background-size: contain;
            }
        }

        &__select-text {
            color: #FFF;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 1;
            text-transform: uppercase;
        }

        &__dropdown {
            border: 1px solid transparent;
            width: 100%;
            padding-bottom: 8px;

            ul {
                li {
                    color: #FFFEFF;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 1;
                    padding: 6px 8px;
                    text-transform: uppercase;

                    &:hover {
                        background: #1ED6FF;
                        color: #10130B;
                    }
                }
            }
        }
    }
}

.pre-title {
    color: rgb(20, 19, 19);
    font-size: 18px;
    font-style: italic;
    font-weight: 600;
    line-height: 1.33;
    margin-bottom: 16px;

    @include max-break(1540px) {
        margin-bottom: 10px;
    }

    @include max-break(850px) {
        font-size: 24px;
    }

    @include max-break(570px) {
        font-size: 16px;
        margin-bottom: 8px;
    }

    @include max-break(480px) {
        font-size: 14px;
        text-align: center;
        max-width: 75%;
        margin-left: auto;
        margin-right: auto;
    }
}

