ol {
    padding-left: 20px;

    li {
        list-style-type: decimal;
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 1.2;
        margin-bottom: 15px;
    }
}

.swiper {
    &.swiper--feedbacks {
        .swiper-pagination-bullet {
            background: rgba(151, 200, 216, 1);
            transition: ease-out 0.35s;
        }

        .swiper-pagination-bullet-active {
            background: rgba(0, 84, 103, 1);
            transition: ease-out 0.35s;
        }
    }

    &.swiper--props {
        .swiper-pagination-bullet {
            background: rgba(151, 200, 216, 1);
            transition: ease-out 0.35s;
        }

        .swiper-pagination-bullet-active {
            background: white;
            transition: ease-out 0.35s;
        }
    }

    &.swiper--benefits .swiper-slide {
        height: auto;
    }
}

.not_visible {
    display: none;
}

.contact-item {
    font-size: 18px;
    font-style: italic;
    font-weight: 700;
    line-height: 1.33;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;

    @include max-break(1280px) {
        font-size: 16px;
    }

    @include max-break(768px) {
        font-size: 18px;
    }

    @include max-break(570px) {
        font-size: 14px;
    }

    &:hover {

        .contact-icon {
            background: var(--wp--gradient--orange-to-lilac);
        }
    }
}

.contact-icon {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin-right: 12px;
    background: rgba(20, 19, 19, 0.05);
    position: relative;
    z-index: 1;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;

    &:before {
        position: absolute;
        content: '';
        display: block;
        left: 50%;
        top: 50%;
        width: 21px;
        height: 21px;
        transform: translate(-50%, -50%);
        z-index: 1;
    }
}

.contact__phone {
    .contact-icon {
        &:before {
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }
    }

    &:hover {
        .contact-icon {
            &:before {
