@keyframes shake {
    0%, 100% {
        transform: translate(-50%, -50%);
    }
    30%, 90% {
        transform: translate(-50%, calc(-50% - 5px));
    }
    60% {
        transform: translate(-50%, -50%);
    }
}

@include max-break('1199px') {
    @keyframes shake {
        0%, 100% {
            transform: translate(-50%, -50%);
        }
        30%, 90% {
            transform: translate(-50%, calc(-50% - 2px));
        }
        60% {
            transform: translate(-50%, -50%);
        }
    }
}


